.clients {
  padding-top: 94px;
  padding-bottom: 94px;
}

.client-list {
  display: flex;
}

.client-list-item {
  width: 170px;
  height: 90px;
  border: 1px solid #AFB1B8;
  border-radius: 4px;
  transition-property: fill, border;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.client-list-item {
  width: calc((100%-150px) / 6);
  margin-right: 30px;
}

.client-list-item:nth-child(6n) {
  margin-right: 0;
}

.client-link {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  transition-property: color, border;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.client-logo {
  fill: #AFB1B8;
}

.client-list-item:hover {
  border-color: #2196F3;
}

.client-list-item:hover .client-logo {
  fill: #2196F3;
  transition-property: fill, border;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.client-link:focus {
  border-color: #2196F3;
}

.client-link:focus .client-logo {
  fill: #2196F3;
  transition-property: fill, border;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.logo-1 {
  color: #FFFFFF;
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  font-size: 26px;
  line-height: 1.19;
}

.logo-1 span {
  color: #2196F3;
}

.contacts-1 {
  padding-top: 60px;
  padding-bottom: 60px;
  font-family: "Roboto";
  font-weight: 400px;
  font-size: 14px;
