}

.adress-1 .item-1:not(:last-child) {
  margin-bottom: 9px;
}

.social-footer-1 .social-1 {
  display: inline-block;
  height: 44px;
  width: 44px;
}

.social-footer-1 .social-1 {
  margin-right: 10px;
  padding-bottom: 97px;
}

.social-footer-1 .social-1:last-child {
