}

.footer-link {
  display: flex;
  background: rgba(255, 255, 255, 0.1);
  width: 44px;
  height: 44px;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition-property: color, background-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.footer-sotial {
  display: block;
  width: 20px;
  height: 20px;
  fill: #FFFFFF;
}

.footer-link:hover,
.footer-link:focus {
  background-color: #2196F3;
}

.footer-link:hover .footer-sotial,
.footer-link:focus .footer-sotial {
  fill: #FFFFFF;
}

.footer-field {
  height: 50px;
  width: 358px;
  margin: 0;
  margin-right: 12px;
  padding: 16px 15px;
  background-color: #2F303A;
  border: 1px solid rgba(255, 255, 255, 0.6);
  box-sizing: border-box;
  border-radius: 4px;
}

.footer-field textarea::placeholder {
  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  display: flex;
  align-items: center;
  letter-spacing: 0.03em;
  color: rgba(255, 255, 255, 0.6);
}

.footer-button {
  display: inline-flex;
  align-items: center;
  padding-top: 10px;
  padding-right: 29px;
  padding-bottom: 10px;
  padding-left: 29px;
  background: #2196F3;
  border: 1px solid #2196F3;
  border-radius: 4px;
  color: #FFFFFF;
  font-family: "Roboto";
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 30px;
  letter-spacing: 0.06em;
  text-decoration: none;
  cursor: pointer;
}

.footer-button::after {
  content: "";
  width: 24px;
  height: 24px;
  margin-left: 10px;
  background-image: url(../img/icons/icon-send.svg);
}

.filter {
  padding-top: 94px;
  padding-bottom: 94px;
}

.project-list {
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  padding-inline-start: 0;
}

.project-list .item {
  display: block;
  width: calc((100% - 60px) / 3);
  margin-right: 30px;
  margin-bottom: 30px;
  box-sizing: border-box;
}

.project-list .item:nth-child(3n) {
  margin-right: 0px;
}

.project-list .item:nth-last-child(-n+3) {
  margin-bottom: 0px;
}

.project-list a {
  display: block;
  min-width: 100%;
  background-color: #FFFFFF;
  transition-property: box-shadow;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.project-list a:hover,
.project-list a:focus {
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.16);
}

.overlay {
  position: relative;
  overflow: hidden;
}

.actions {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  bottom: 0;
  left: 0;
  transform: translateY(100%);
  padding-top: 63px;
  padding-right: 24px;
  padding-bottom: 63px;
  padding-left: 24px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 0.03em;
  color: #FFFFFF;
  background-color: rgba(33, 150, 243, 0.9);
  transition-property: opacity, transform;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.project-list a:hover p,
.project-list a:focus p {
  opacity: 1;
  transform: translateY(0);
}

.card-contant {
  display: block;
  padding-top: 20px;
